.form {
  max-width: 350px;
  border: 2px solid #000;
  border-radius: 10px;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.form p {
  margin: 0;
}

.form p:not(:last-child) {
  margin-bottom: 5px;
}

.form input {
  width: 100%;
  padding: 5px;
}

.form button {
  align-self: center;
  padding: 5px 10px;
}

